Template.bertAlert.helpers({                                                                                 // 10
  alert: function(){                                                                                         // 11
    // Get and return our bertAlert session variable to our alert template.                                  // 12
    var bert = Session.get('bertAlert');                                                                     // 13
    return bert;                                                                                             // 14
  }                                                                                                          // 15
});                                                                                                          // 16
                                                                                                             // 17
